import React, { useState } from "react";
import styled from "styled-components";
import { Button, InputGroup, Form, Alert, FormControl } from 'react-bootstrap';
import { request } from "@octokit/request";

import ReadOnlyEditorSec from "./ReadOnlyEditorSec";

const Element = ({ className, pinRequired, pin, point, showAnswer, handleHTMLEditorChange, handleCSSEditorChange, handleShowAnswer, resembleNum, handleClose, qHTML, qCSS, btnClick }) => {
  const [teacherCodeId, setTeacherCodeId] = useState("");
  const [validated, setValidated] = useState("");
  const [inputPassword, setInputPassword] = useState("");
  return <div className={className}>
    <div className="d-grid gap-2 h-100 w-100">
      {
        ((point < 99 || point === 99) && resembleNum < 3) && btnClick==="sendAnsBtn" &&
        <>
          <h5>真可惜！這次沒有通過關卡唷！</h5>
          <Button variant="secondary" size="lg" onClick={handleClose}>
            繼續挑戰
          </Button>
        </>
      }

      {
        (((point < 99 || point === 99) && (resembleNum === 3 || resembleNum > 3)) && !showAnswer) && btnClick==="sendAnsBtn" && pinRequired==="False" &&
        <>
          <h5>真可惜！這次沒有通過挑戰題唷！</h5>
          <Button variant="secondary" size="lg" onClick={handleClose}>
            返回挑戰題
          </Button>
          <Button variant="success" size="lg" onClick={() => {
            handleShowAnswer(true);
          }}>
            查看老師的作法
          </Button>
        </>
      }
      {
        (((point < 99 || point === 99) && (resembleNum === 3 || resembleNum > 3)) && !showAnswer) && btnClick==="sendAnsBtn" && pinRequired==="True" &&
        <>
          <h5>真可惜！這次沒有通過挑戰題唷！</h5>
          <Button variant="secondary" size="lg" onClick={handleClose}>
            返回挑戰題
          </Button>
          <InputGroup className="mb-3">
            <FormControl
              placeholder="請輸入密碼來查看老師的作法"
              aria-label="Recipient's username"
              aria-describedby="basic-addon2"
              onChange={(e)=>{
                let val = e.target.value;
                setInputPassword(val)
              }}
            />
            <Button size="lg" variant="outline-success" id="button-addon2"
              onClick={() => {
                if(inputPassword === pin){
                  handleShowAnswer(true);
                }
              }}
            >
              送出
            </Button>
          </InputGroup>
        </>
      }

      {
        ((point < 99 || point === 99) && showAnswer) && btnClick==="sendAnsBtn" &&
        <>
          <h5>真可惜！這次沒有通過挑戰題唷！</h5>
          <ReadOnlyEditorSec language="html" code={qHTML} />
          <ReadOnlyEditorSec language="css" code={qCSS} />
          <Button variant="secondary" size="lg"
            onClick={() => {
              handleClose();
            }}>
            返回挑戰題
          </Button>
        </>
      }

      {
        point > 99 && !showAnswer && btnClick==="sendAnsBtn" &&
        <>
          <h5>恭喜你！通過挑戰題囉！</h5>
          <Button variant="secondary" size="lg" onClick={handleClose}>
            返回挑戰題
          </Button>
          <Button variant="success" size="lg" onClick={() => {
            handleShowAnswer(true);
          }}>
            查看老師的作法
          </Button>
        </>
      }

{
        point > 99 && showAnswer && btnClick==="sendAnsBtn" &&
        <>
          <h5>恭喜你！通過挑戰題囉！</h5>
          <ReadOnlyEditorSec language="html" code={qHTML} />
          <ReadOnlyEditorSec language="css" code={qCSS} />
          <Button variant="secondary" size="lg" onClick={handleClose}>
            返回挑戰題
          </Button>
        </>
      }

      {
        btnClick==="sendPINBtn" &&
        <> 
          <div className="d-flex justify-content-between align-content-end">
            <h5>請輸入程式碼序號</h5>
            <h5 
              style={{
                fontSize: ".2rem", 
                color: teacherCodeId.length !== 32 ? "#aaa" : "green",
              }}
            >{teacherCodeId.length} / 32</h5>
          </div>
          <Form>
            <Alert bsPrefix={`alert alert-success myalert ${validated === "true" && "op-1"} ${validated === "false" && "op-0"}`}>
              授權碼載入成功
            </Alert>
            <Alert bsPrefix={`alert alert-danger myalert ${validated === "false" && "op-1"} ${validated === "true" && "op-0"}`}>
              授權碼載入失敗
            </Alert>
            <InputGroup>
              <Form.Control
                placeholder=""
                aria-label="Recipient's username"
                aria-describedby="basic-addon2"
                bsPrefix={'form-control my-1 bdr0'}
                onChange={e => setTeacherCodeId(e.target.value)}
              />
              <Button
                disabled={teacherCodeId.length !== 32 ? true : false}
                bsPrefix={'my-1 btn btn-outline-secondary btn-lg'}
                id="basic-addon2"
                onClick={() => {
                  const getTeacherCode = async (gist_id) => {
                    const Form = document.getElementsByTagName("FORM")[0]
                    const Input = Form.getElementsByTagName("INPUT")[0]
                    await request('GET /gists/{gist_id}', { gist_id }).catch(err => {
                      setValidated("false");
                      Input.value = "";
                      setTeacherCodeId("");
                      setTimeout(function () {
                        setValidated("");
                      }, 1000);
                      return err;
                    }).then((e) => {
                      let { status } = e;
                      if (status !== 200) return;
                      let { data } = e, { files } = data;
                      let html = files["index.html"]["content"];
                      let css = files["styles.css"]["content"];
                      handleHTMLEditorChange(html)
                      handleCSSEditorChange(css)
                      setValidated("true");
                      setTeacherCodeId("")
                      Input.value = "";
                      setTimeout(function () {
                        setValidated("");
                      }, 1000);
                      handleClose();
                    });
                  }
                  getTeacherCode(teacherCodeId);
                }}>
                送出
              </Button>
            </InputGroup>
          </Form>
          <Button variant="secondary" size="lg"
            onClick={() => {
              handleClose();
            }}>
            返回挑戰題
          </Button>
        </>
      }
    </div>
  </div >;
};

const StyledElement = styled(Element)`
  width: 100%;
  height: 100%;
  .bdr0 {
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
  }
  .myalert {
    z-index: 2000;
    position: fixed;
    right: 20px;
    bottom: 20px;
    transition: all 0.5s ease-out;
    opacity: 0
  }
  .op-0 {
    opacity: 0
  }
  .op-1 {
    opacity: 1
  }
  
`;

export default StyledElement;
