import React, { useState, useEffect } from "react";
import styled from "styled-components";
import QuestionImgXML from "./QuestionImgXML";
import { OverlayTrigger, Tooltip } from 'react-bootstrap';


const Element = ({ className, answerHTML, answerCSS, play }) => {
  let [state, setState] = useState({ active: false });
  function initComparisons() {
    let imgOverlay = document.getElementsByClassName("img-comp-overlay")[0];
    let compareImages = (e) => {
      // var slider, img, clicked = 0, w, h;
      /* Get the width and height of the img element */
      let img = e;
      let w = img.offsetWidth;
      // let h = img.offsetHeight;
      let clicked = 0;
      /* Set the width of the img element to 50%: */
      img.style.width = (w / 2) + "px";
      /* Create slider: */
      let slider = document.createElement("DIV");
      slider.setAttribute("class", "img-comp-slider");
      /* Insert slider */
      img.parentElement.insertBefore(slider, img);
      /* Position the slider in the middle: */
      // slider.style.top = (h / 2) - (slider.offsetHeight / 2) + "px";
      slider.style.left = (w / 2) - (slider.offsetWidth / 2) + "px";
      /* Execute a function when the mouse button is pressed: */
      slider.addEventListener("mousedown", slideReady);
      /* And another function when the mouse button is released: */
      window.addEventListener("mouseup", slideFinish);
      /* Or touched (for touch screens: */
      slider.addEventListener("touchstart", slideReady);
       /* And released (for touch screens: */
      window.addEventListener("touchend", slideFinish);
      function slideReady(e) {
        /* Prevent any other actions that may occur when moving over the image: */
        e.preventDefault();
        /* The slider is now clicked and ready to move: */
        clicked = 1;
        /* Execute a function when the slider is moved: */
        window.addEventListener("mousemove", slideMove);
        window.addEventListener("touchmove", slideMove);
      }
      function slideFinish() {
        /* The slider is no longer clicked: */
        clicked = 0;
      }
      function slideMove(e) {
        var pos;
        /* If the slider is no longer clicked, exit this function: */
        if (clicked === 0) return false;
        /* Get the cursor's x position: */
        pos = getCursorPos(e)
        /* Prevent the slider from being positioned outside the image: */
        if (pos < 0) pos = 0;
        if (pos > w) pos = w;
        /* Execute a function that will resize the overlay image according to the cursor: */
        slide(pos);
      }
      function getCursorPos(e) {
        var a, x = 0;
        e = (e.changedTouches) ? e.changedTouches[0] : e;
        /* Get the x positions of the image: */
        a = img.getBoundingClientRect();
        /* Calculate the cursor's x coordinate, relative to the image: */
        x = e.pageX - a.left;
        /* Consider any page scrolling: */
        x = x - window.pageXOffset;
        return x;
      }
      function slide(x) {
        /* Resize the image: */
        img.style.width = x + "px";
        /* Position the slider: */
        slider.style.left = img.offsetWidth - (slider.offsetWidth / 2) + "px";
      }
    }
    compareImages(imgOverlay);
  }
  useEffect(() => {
    initComparisons();
    let myCSSWidth = document.getElementById("myCSS").offsetWidth;
    let displayBox = document.getElementById("displayBox");
    let questionBundle = document.querySelectorAll(".questionBundle")[1];
    displayBox.style.width = myCSSWidth + "px";
    questionBundle.style.width = myCSSWidth + "px";
  }, []);
  return (
    <div className={className}>
      <div
        id="flexSwitch"
        className="d-flex p-0 align-items-end form-check form-switch position-absolute"
      >
        <OverlayTrigger
          placement="top"
          overlay={
            <Tooltip id="tooltip-top">
              題目與作答圖形分割畫面
            </Tooltip>
          }
        >
          <input
            className="form-check-input m-0 me-1"
            type="checkbox"
            id="flexSwitchCheckChecked"
            defaultChecked={state.active}
            onChange={(e) => {
              setState({ active: !state.active })
              const slider = document.querySelectorAll(".img-comp-slider")[0];
              if(e.target.checked){
                slider.style.visibility = "visible";
              }else{
                slider.style.visibility = "hidden";
              }
            }}
          />
        </OverlayTrigger>
        
      </div>
      <div id="myCSS" dangerouslySetInnerHTML={{ __html: answerHTML }} />
      <style dangerouslySetInnerHTML={{ __html: answerCSS }} />
      <div id="displayBox">
        < div
          id="opDisplay"
          className="img-comp-overlay"
          style={{
            visibility: state.active ? "visible" : "hidden"
          }}
        >
          <QuestionImgXML play={play}  />
        </div>
      </div>
    </div >
  );
};

const StyledElement = styled(Element)`
  width: 100%;
  height: 100%;
  opacity: 1;
  background-color: #fff;
  justify-content: center;
  align-items: center;
  position: relative;
  .img-comp-slider {
    visibility: hidden;
    position: absolute;
    z-index: 9;
    cursor: ew-resize;
    /*set the appearance of the slider:*/
    width: 2px;
    height: 100%;
    background-color: #dee2e6;
    /*opacity: 0.7;*/
    border-radius: 5px;
    z-index: 20;
    top: 0;
  }
  .img-comp-slider::after {
    content: "";
    display: block;
    width: 10px;
    height: 50px;
    background: #dee2e6;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 30;
  }
  #rangeBox{
    display: flex;
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 15;
    top: 96%;
    left: 50%;
    transform: translate(-50%, -50%);
    align-items: center;
  }

  #rangeBox input {
    height: 0;
    border-radius: 0;
  }

  .w-200 {
    width: 200%;
  }
  #flexSwitch {
    color: #aaa;
    right: 10px;
    top: 8px;
    line-height: 1;
    z-index: 12;
    label {
      font-size: 0.75rem;
    }
  }

  #myCSS {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #fff;
    z-index: 10;
    overflow: hidden;
  }

  #opDisplay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #fff;
    z-index: 11;
    visibility: hidden;
    overflow: hidden;
  }

  #displayBox {
    position: relative;
    width: 100%;
    height: 100%;
  }
`;

export default StyledElement;
