import React, { useState, useEffect, useMemo } from "react";
import { useParams } from "react-router-dom";
import { Modal, Spinner } from 'react-bootstrap';

import FadeLoader from "react-spinners/FadeLoader";
import { css } from "@emotion/react";

import cssjs from "jotform-css.js";
import styled from "styled-components";
import html2canvas from "html2canvas";
import resemble from "resemblejs";

import QuestionTxtSec from "./QuestionTxtSec";
import ColorSec from "./ColorSec";
import QuestionImgSec from "./QuestionImgSec";
import AnswerImgSec from "./AnswerImgSec";
import EditorSec from "./EditorSec";
import ModalContentDisplay from "./ModalContentDisplay";

// https://docs.google.com/spreadsheets/d/1YqXTHC4lYco6Pd0OTG6J0mPgroRW0g2uWJ4_HktI5YI/edit#gid=0
// Can be a string as well. Need to ensure each key-value pair ends with ;

const override = css`
  display: block;
  margin: 0 auto;
`;

const Element = ({ className }) => {
    let { topicId } = useParams();
    let [loading, setLoading] = useState({ status: true });
    const [play, setPlay] = useState({});

    const [qHTML, setQHTML] = useState("");
    const [qCSS, setQCSS] = useState("");
    const [qCSSParsed, setQCSSParsed] = useState("");

    const [answerHTML, setAnswerHTML] = useState("");
    const [answerCSS, setAnswerCSS] = useState("");

    const [btnClick, setBtnClick] = useState("");

    const handleHTMLEditorChange = (code) => {
        setAnswerHTML(code)
    };
    const handleCSSEditorChange = (code) => {
        setAnswerCSS(code)
    };

    const myCSSParsed = useMemo(() => {
        let parser = new cssjs.cssjs();
        let parsed = parser.parseCSS(answerCSS);
        let mapParsed = parsed.map(e => {
            if (e.selector.split(',').length === 0) {
                return "#myCSS " + e.selector + " {\n" +
                    e.rules.map(f => f.directive + ": " + f.value + ";").join("\n") + "}"
            } else {
                return e.selector.split(',').map(g => "#myCSS " + g).join(",") + " {\n" +
                    e.rules.map(f => f.directive + ": " + f.value + ";").join("\n") + "}"
            }
        });
        return mapParsed.map(e => e + "\n").join("\n");
    }, [answerCSS]);

    const qparsed = (css, id) => {
        let parser = new cssjs.cssjs();
        let parsed = parser.parseCSS(css);
        let mapParsed = parsed.map(e => {
            if (e.selector.split(',').length === 0) {
                return `${id} ${e.selector} {\n` +
                    e.rules.map(f => f.directive + ": " + f.value + ";").join("\n") + "}"
            } else {
                return e.selector.split(',').map(g => `${id} ${g}`).join(",") + " {\n" +
                    e.rules.map(f => f.directive + ": " + f.value + ";").join("\n") + "}"
            }
        });
        return mapParsed.map(e => e + "\n").join("\n");
    }

    useEffect(() => {
        if (topicId === undefined) return;
        const gasUrl = "https://script.google.com/macros/s/AKfycbxwgFFR9XwjCwempFDWaMnwmY9T19k0F36aLSMYnSvAUIdeRYCLeL2cN4PKVE-2JIUXAw/exec";
        const uri = `${gasUrl}?playId=${topicId}`;
        fetch(uri)
            .then(function (response) {
                return response.json();
            })
            .then(function (myJson) {
                if (myJson.status === "false") {
                    console.log("沒有此頁面")
                } else {
                    if (myJson.length === 1) {
                        setPlay(myJson[0])
                    } else {
                        console.log("列表頁面")
                    }
                }
            });
    }, [topicId]);

    useEffect(() => {
        if (play.aDefaultHtml === undefined || play.aDefaultCss === undefined) return;
        setAnswerHTML(play.aDefaultHtml);
        setAnswerCSS(play.aDefaultCss);
        setQHTML(play.qHtml);
        setQCSS(play.qCss);
        setLoading({ status: false });
        setQCSSParsed(qparsed(play.qCss, ".questionBundle"))
        setPinRequired(play.pinRequired)
        setPin(play.pin)
    }, [play]);

    
    const [pinRequired, setPinRequired] = useState(false);
    const [pin, setPin] = useState("");
    const [show, setShow] = useState(false);
    const [point, setPoint] = useState(0);
    const [resembleLoading, setResembleLoading] = useState({ status: false });
    const [resembleNum, setResembleNum] = useState(0);
    const [showAnswer, setShowAnswer] = useState(false);

    const handleShowAnswer = (e) => {
        setShowAnswer(e);
    };

    const handleClose = () => {
        setShow(false)
    };

    const handleResembleCanvas = async () => {
        const myCSSElem = document.querySelector("#myCSS");
        const beComparedElem = document.querySelector("#beCompared");
        let myCSSDataURL = await html2canvas(myCSSElem, { backgroundColor: null }).then((canvas) => canvas.toDataURL("image/png", 1.0));
        let beComparedDataURL = await html2canvas(beComparedElem, { backgroundColor: null }).then((canvas) => canvas.toDataURL("image/png", 1.0));
        resemble.outputSettings({ useCrossOrigin: false });
        resemble(myCSSDataURL).compareTo(beComparedDataURL).onComplete((data) => {
            const roundDecimal = (val, precision) => Math.round(Math.round(val * Math.pow(10, (precision || 0) + 1)) / 10) / Math.pow(10, (precision || 0));
            let tem = 100 - data.misMatchPercentage;
            let temp = isNaN(tem) ? 0 : roundDecimal(tem, 2);
            setResembleLoading({ status: false });
            setPoint(temp);
            setShow(true);
        });
        return `done`;
    }

    return (
        <div className={className}>
            <Modal bsPrefix='modal' centered aria-labelledby="contained-modal-title-vcenter"
                onExited={() => {
                    setPoint(0);
                    setResembleLoading({ status: false });
                    setShowAnswer(false);
                }}
                show={show}
                onHide={handleClose}
                backdrop="static"
            >
                <Modal.Body bsPrefix={
                    `modal-body 
                    ${(point < 99 || point === 99) && btnClick==="sendAnsBtn" && !showAnswer && resembleNum < 3 ? "modal-body-fail1" : ""}
                    ${(point < 99 || point === 99) && btnClick==="sendAnsBtn" && !showAnswer && (resembleNum === 3 || resembleNum > 3) ? "modal-body-fail2" : ""}
                    ${(point > 99) && btnClick==="sendAnsBtn" && !showAnswer ? "modal-body-success" : ""}
                    ${btnClick==="sendPINBtn" ? "modal-body-answer" : ""}`
                }>
                    <ModalContentDisplay
                        pin={pin}
                        pinRequired={pinRequired}
                        handleHTMLEditorChange={handleHTMLEditorChange}
                        handleCSSEditorChange={handleCSSEditorChange}
                        btnClick={btnClick}
                        point={point}
                        showAnswer={showAnswer}
                        handleShowAnswer={handleShowAnswer}
                        resembleNum={resembleNum}
                        handleClose={handleClose}
                        qHTML={qHTML}
                        qCSS={qCSS}
                    />
                </Modal.Body>
            </Modal>
            <div
                className="loadingPage"
                style={{
                    visibility: loading.status ? "visible" : "hidden",
                    opacity: loading.status ? 1 : 0,
                    zIndex: loading.status ? 999 : -999
                }}
            >
                <div
                    className="sweet-loading"
                    style={{
                        visibility: loading.status ? "visible" : "hidden",
                        opacity: loading.status ? 1 : 0,
                        zIndex: loading.status ? 999 : -999
                    }}
                >
                    <FadeLoader color="#ffffff" css={override} size={10} />
                </div>
            </div>
            <div className="mainPage px-3 pb-3">
                <div className="col-8 d-flex flex-column justify-content-end">
                    <div className="h-36 p-0 pb-2 pe-1">
                        <div className="h-25 pb-2">
                            <div className="h-100 d-flex align-items-end">
                                <img
                                    className="me-3"
                                    src="https://cdn1.koding.school/assets/logo/oa-logo-mini-730fe59f98d967929255f884abf83ff8f3934387d50b67403be1c4cd1eac6561.png"
                                    alt=""
                                    height="100%"
                                />
                                <h1 id="titleName">/ Challenge #{play.gamesID} - {play.gamesName} / #{play.playId} - {play.title}</h1>
                            </div>
                        </div>
                        <div className="h-75 pt-2">
                            <div className="d-flex h-100">
                                <div className="col-9 pe-2">
                                    <QuestionTxtSec play={play} />
                                </div>
                                <div className="col-3">
                                    <ColorSec play={play} />
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="h-60 p-0 pt-2 pe-1 d-flex">
                        <div className="col pe-2">
                            <QuestionImgSec qCSSParsed={qCSSParsed} play={play} />
                        </div>
                        <div className="col">
                            <AnswerImgSec
                                play={play}
                                qCSSParsed={qCSSParsed}
                                answerHTML={answerHTML}
                                answerCSS={myCSSParsed}
                            />
                        </div>
                    </div>
                </div>
                <div className="col-4 d-flex flex-column justify-content-end ps-1">
                    <div className="h-40 pb-2 position-relative">
                        <EditorSec
                            language="HTML"
                            handleEditorChange={handleHTMLEditorChange}
                            code={answerHTML}
                        />
                    </div>
                    <div className="h-40 pb-2 position-relative">
                        <EditorSec
                            language="CSS"
                            handleEditorChange={handleCSSEditorChange}
                            code={answerCSS}
                        />
                    </div>
                    <div className="h-6 position-relative d-flex">
                        <div className="col d-flex pe-1">
                            <button id="sendPINBtn" className="btn btn-light w-100" type="button"
                                onClick={()=>{
                                    setBtnClick("sendPINBtn");
                                    setShow(true);
                                }}  
                            >
                                <i className="bi bi-cloud-arrow-up-fill me-2"></i>匯入程式碼
                            </button>
                        </div>
                        <div className="col d-flex ps-1">
                            <button id="sendAnsBtn" className="btn btn-light w-100" type="button" disabled={false}
                                onClick={()=>{
                                    setBtnClick("sendAnsBtn");
                                    setResembleNum(resembleNum + 1)
                                    setResembleLoading({ status: true });
                                    handleResembleCanvas();
                                }}  
                            >
                                {
                                    resembleLoading.status
                                    &&
                                    <Spinner
                                        as="span"
                                        animation="border"
                                        size="sm"
                                        role="status"
                                        aria-hidden="true"
                                        className="me-2"
                                    />
                                }
                                {
                                    !resembleLoading.status
                                    &&
                                    <>
                                      <i className="bi bi-cursor-fill me-2"></i>
                                      <span>送出答案</span>
                                    </>
                                }
                            </button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};

const StyledElement = styled(Element)`
  width: 100vw;
  height: 100vh;
  position: relative; 

  .mainPage {
    width: 100%;
    height: 100%;
    background-color: #363732;
    position: absolute; 
    display: flex; 
    z-index: 0;
  }

  .loadingPage{
    width: 100%;
    height: 100%;
    position: absolute; 
    background-color: #363732;
    opacity: 1;
    visibility: visible;
    transition: All 0.2s ease-out;
  }

  .sweet-loading {
    width: 100%;
    height: 100%;
    display: flex; 
    align-items: center;
    position: absolute; 
    left: 50%;
    transform: translateX(-50%);
    background-color: #222;
    opacity: 1;
    visibility: visible;
    transition: All 0.2s ease-out;
  }  

  #titleName {
    font-size: 1.2rem;
    font-family: 'Fira Code',monospace;
    line-height: 1;
    color: #fff;
    margin: 0;
    padding-bottom: 10px;
  }
  .h-36 {
    height: 36%;
  }
  .h-40 {
    height: 44.5%;
  }
  .h-6 {
    height: 6%;
  }
  .h-60 {
    height: 60%;
  }
  #sendPINBtn, #sendAnsBtn {
    background: white;
    color: #222;
    border: 5px solid #dee2e6;

  }
  
  #sendPINBtn:hover {
    background: #11BCAB;
    color: #fff;
    border: 5px solid #11BCAB;
  }
  #sendAnsBtn:hover {
    background: #FE5958;
    color: #fff;
    border: 5px solid #FE5958;

  }
`;

export default StyledElement;
